'use client';

import { usePathname } from 'next/navigation';
import Link from 'next/link';
import { cn } from '@/lib/utils';
import {
  Home,
  Car,
  Bell,
  Activity,
  Wrench,
  BarChart3,
  Shield
} from 'lucide-react';

const navigation = [
  { name: 'Dashboard', href: '/', icon: Home },
  { name: 'Fleet', href: '/vehicles', icon: Car },
  { name: 'Maintenance', href: '/maintenance', icon: Wrench },
  { name: 'Reports', href: '/reports', icon: BarChart3 },
  { name: 'Notifications', href: '/notifications', icon: Bell },
  { name: 'Security', href: '/settings', icon: Shield },
];

export function Navigation() {
  const pathname = usePathname();

  return (
    <nav className="glass-surface sticky top-0 z-40">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between h-16">
          <div className="flex">
            <div className="flex-shrink-0 flex items-center">
              <div className="flex items-center gap-2">
                <div className="w-9 h-9 rounded-xl bg-gradient-to-br from-blue-500 via-cyan-400 to-emerald-400 flex items-center justify-center shadow-lg shadow-blue-500/30">
                  <Activity className="h-5 w-5 text-slate-950" />
                </div>
                <span className="text-xl font-bold text-white">HMC Tracker</span>
              </div>
            </div>
            <div className="hidden sm:ml-6 sm:flex sm:space-x-8">
              {navigation.map((item) => {
                const isActive = item.href === '/'
                  ? pathname === '/'
                  : item.href === '/vehicles'
                  ? pathname.startsWith('/vehicles') || pathname.startsWith('/vehicle/')
                  : pathname.startsWith(item.href);
                return (
                  <Link
                    key={item.name}
                    href={item.href}
                    className={cn(
                      'inline-flex items-center px-1 pt-1 border-b-2 text-sm font-semibold transition-colors',
                      isActive
                        ? 'border-blue-400 text-white'
                        : 'border-transparent text-slate-300 hover:border-blue-300 hover:text-white'
                    )}
                  >
                    <item.icon className="h-4 w-4 mr-2" />
                    {item.name}
                  </Link>
                );
              })}
            </div>
          </div>
          <div className="flex items-center">
            <div className="text-sm text-slate-300">
              Hamad Hospital â€¢ Equipment Management
            </div>
          </div>
        </div>
      </div>
    </nav>
  );
}